<template>
  <div class="wang-editor">
    <Toolbar :editor="editor" :defaultConfig="toolbarConfig" :mode="mode" class="tool-bar"/>
    <Editor v-model="raw_html" :defaultConfig="editor_config" :mode="mode" @onCreated="handleEditorCreated"
            class="editor-zone"/>
  </div>
</template>

<script>
import '@wangeditor/editor/dist/css/style.css' // 引入 css
import { Editor, Toolbar } from '@wangeditor/editor-for-vue';
import { Boot } from '@wangeditor/editor';
import { merge } from 'lodash';

// editor为空获取value值存在"<p><br></p>"，在表单中无法校验，借助于插件解决
function withEmptyHtml(editor) {
  const { getHtml } = editor
  const newEditor = editor

  newEditor.getHtml = () => {
    if (newEditor.isEmpty()) {
      return ''
    }
    return getHtml()
  }

  return newEditor
}

// 注册插件
Boot.registerPlugin(withEmptyHtml);

export default {
  components: {
    Toolbar,
    Editor,
  },
  props: {
    value: {
      type: String,
      required: true,
    },
    mode: {
      type: String,
      default: 'default',
    },
    toolbarConfig: {
      type: Object,
      default: () => {
        return {}
      }
    },
    editorConfig: {
      type: Object,
      default: (rawProps) => {
        return {}
      }
    },
  },
  data() {
    return {
      editor: null,
      // 默认editorConfig属性
      default_editor_config: {
        MENU_CONF: {
          uploadImage: {
            fieldName: 'file',
            server: `${process.env.VUE_APP_SERVER}/upload/editor/`,
            headers: {
              Authorization: sessionStorage.access_token,
            },
          }
        }
      }
    }
  },
  computed: {
    editor_config() {
      return merge({}, this.default_editor_config, this.editorConfig)
    },
    raw_html: {
      set(value) {
        this.$emit('input', value);
      },
      get() {
        return this.value;
      }
    },
  },
  methods: {
    // 富文本编译器的初始化
    handleEditorCreated(editor) {
      this.editor = Object.seal(editor) // 一定要用 Object.seal() ，否则会报错
    },
  },
  beforeDestroy() {
    const editor = this.editor
    if (editor == null) return
    editor.destroy() // 组件销毁时，及时销毁编辑器
  },
}

</script>

<style lang="less" scoped>
.wang-editor {
  border: 1px solid #ccc;
  border-radius: 3px;
  overflow: hidden;

  .tool-bar {
    border-bottom: 1px solid #ccc
  }

  .editor-zone {
    height: 600px;
    overflow-y: hidden;
  }
}
</style>